const zoomables = document.querySelectorAll("main img:not(.nozoom)");
for (let img of zoomables) {
    // Extract the image dimensions.
    const imageWidth = img.getAttribute("width");
    const imageHeight = img.getAttribute("height");

    // Override the link click event so it opens up a modal of the image.
    img.style.cursor = "zoom-in";
    img.addEventListener("click", (e) => {
        // Stop the browsers default behavior of opening the link.
        e.preventDefault();

        // Grab the browser dimensions each time in case the user resized their browser.
        const browserWidth = window.innerWidth;
        const browserHeight = window.innerHeight;

        // Compute the scaling factor for image so it never exceeds the browsers dimensions.
        let scalingFactor = 1.0;
        if ((imageWidth > browserWidth) || (imageHeight > browserHeight)) {
            scalingFactor = Math.min(browserWidth / imageWidth, browserHeight / imageHeight);
        }

        // Create a new image tag to present the image in the modal.
        const modalImg = document.createElement("img");
        modalImg.setAttribute("src", img.getAttribute("src"));
        modalImg.setAttribute("width", imageWidth * scalingFactor);
        modalImg.setAttribute("height", imageHeight * scalingFactor);

        // Create the modal container dialog.
        const modalContainer = document.createElement("div");
        modalContainer.classList.add("modal");
        modalContainer.append(modalImg);

        // Add the modal to the page.
        document.body.append(modalContainer);

        // Close the modal on click.
        modalContainer.addEventListener("click", (e) => {
            modalContainer.remove();
        });
    });
}
